* {
  margin: 0;
  padding: 0;
}

body {
  background-color: purple;
  padding: 3em;
}

header img {
  width: 10%;
}

main {
  margin-top: 1.5em;
  display: flex;
  justify-content: space-between;
}

main img {
  width: 45%;
}

.content {
  padding-top: 2em;
  width: 40%;
}

.content h2 {
  font-size: 45px;
  color: white;
}

.content p {
  font-size: 15px;
  margin: 2em 0em;
  color: white;
}

.content button {
  background-color: white;
  color: purple;
  padding: 1.2em;
  width: 40%;
  outline: none;
  cursor: pointer;
  border-radius: 20px;
  border: none;
  font-weight: bold;
}

footer i {
  margin: 0 1em;
}

footer {
  text-align: end;
}
